import { IEmapI18N, IEmapBackgroundItem, IEmapDropItem, IEmapSource, IEmapServer, EEmapDeviceType, EEmapSourceType } from '../index';

export class EmapItem {
    public objectId: string = '';
    public mapName: string = '';

    //#region i18n
    private _i18n: IEmapI18N = {
        or: 'or',
        previewButtonAddMap: 'Add Map',
        previewButtonViewMap: 'View Map',
        modifyEmptyTitle: 'Upload Image of the map',
        modifyEmptyContent: 'Drag & Drop image here',
        modifyButtonUploadImage: 'Click here to upload map',
        removeTitle: 'Are you sure you want to remove this map?',
        removeContent: 'Removing this map will undo all saved device bindings including video device bindings and event source bindings.',
        removeButtonNo: "No, I don't want lose my data",
        removeButtonYes: "Yes, I know what I'm doing",
        deviceSourceListTitle: 'Source Device',
        deviceSourceListSubTitle: 'Drag and drop source devices to the map',
    };
    //#endregion i18n

    //#region Image
    private backgroundItemOriginal: IEmapBackgroundItem = {
        image: '',
        showSize: { width: 0, height: 0 },
    };
    public backgroundItem: IEmapBackgroundItem = { ...this.backgroundItemOriginal };
    //#endregion Image

    //#region Drag & Drop
    private dropItemOriginal: IEmapDropItem = {
        source: null,
        mousePosition: { x: 0, y: 0 },
        size: { width: 0, height: 0 },
    };
    public dropItem: IEmapDropItem = { ...this.dropItemOriginal };
    //#endregion Drag & Drop

    //#region Server
    public videoServer: IEmapServer[] = [];
    public eventServer: IEmapServer[] = [];
    public trackServer: IEmapServer[] = [];
    //#endregion Server

    //#region Source
    public focusSources: IEmapSource[] = [];
    //#endregion Source

    private focusSourceOriginal: IEmapSource = {
        objectId: '',
        index: '',
        name: '',
        sourceType: EEmapSourceType.none,
        deviceType: EEmapDeviceType.none,

        positionScale: null,
        positionShow: null,
    };
    public focusSource: IEmapSource = { ...this.focusSourceOriginal };
    //#endregion Source

    constructor() {}

    //#region i18n get set
    get i18n() {
        return this._i18n;
    }

    set i18n(value: IEmapI18N) {
        if (value != null) {
            let self = this;
            Object.keys(value).map(function(objectKey, index) {
                if (self._i18n[objectKey]) {
                    self._i18n[objectKey] = value[objectKey];
                }
            });
        }
    }
    //#endregion i18n get set

    //#region reset
    public resetSelf(): void {
        this.clearDropItem();
        this.clearFocusSource();
        this.clearServerSource();
    }

    public resetSourcePosition(): boolean {
        if (
            !this.backgroundItem ||
            !this.backgroundItem.showSize ||
            !this.backgroundItem.showSize.width ||
            !this.backgroundItem.showSize.height ||
            this.backgroundItem.showSize.width <= 0 ||
            this.backgroundItem.showSize.height <= 0
        ) {
            return false;
        }

        let backgroundWidth = this.backgroundItem.showSize.width;
        let backgroundHeight = this.backgroundItem.showSize.height;

        // Video Server
        for (let index in this.videoServer) {
            let server = this.videoServer[index];
            for (let jndex in server.sources) {
                let source = server.sources[jndex];
                if (source.positionScale && source.positionScale.horizontal && source.positionScale.vertical) {
                    if (!source.positionShow) {
                        this.videoServer[index].sources[jndex].positionShow = {
                            x: 0,
                            y: 0,
                        };
                    }
                    this.videoServer[index].sources[jndex].positionShow.x = source.positionScale.horizontal * backgroundWidth;
                    this.videoServer[index].sources[jndex].positionShow.y = source.positionScale.vertical * backgroundHeight;
                }
            }
        }

        // Event Server
        for (let index in this.eventServer) {
            let server = this.eventServer[index];
            for (let jndex in server.sources) {
                let source = server.sources[jndex];
                if (source.positionScale && source.positionScale.horizontal && source.positionScale.vertical) {
                    if (!source.positionShow) {
                        this.eventServer[index].sources[jndex].positionShow = {
                            x: 0,
                            y: 0,
                        };
                    }
                    this.eventServer[index].sources[jndex].positionShow.x = source.positionScale.horizontal * backgroundWidth;
                    this.eventServer[index].sources[jndex].positionShow.y = source.positionScale.vertical * backgroundHeight;
                }
            }
        }

        // Track Server
        for (let index in this.trackServer) {
            let server = this.trackServer[index];
            for (let jndex in server.sources) {
                let source = server.sources[jndex];
                if (source.positionScale && source.positionScale.horizontal && source.positionScale.vertical) {
                    if (!source.positionShow) {
                        this.trackServer[index].sources[jndex].positionShow = {
                            x: 0,
                            y: 0,
                        };
                    }
                    this.trackServer[index].sources[jndex].positionShow.x = source.positionScale.horizontal * backgroundWidth;
                    this.trackServer[index].sources[jndex].positionShow.y = source.positionScale.vertical * backgroundHeight;
                }
            }
        }

        // Focus Source
        for (let jndex in this.focusSources) {
            let source = this.focusSources[jndex];
            if (source.positionScale && source.positionScale.horizontal && source.positionScale.vertical) {
                if (!source.positionShow) {
                    this.focusSources[jndex].positionShow = {
                        x: 0,
                        y: 0,
                    };
                }
                this.focusSources[jndex].positionShow.x = source.positionScale.horizontal * backgroundWidth;
                this.focusSources[jndex].positionShow.y = source.positionScale.vertical * backgroundHeight;
            }
        }
    }
    //#endregion reset

    //#region clear
    public clearSelf(): void {
        this.clearBackgroundItem();
        this.clearDropItem();
        this.clearFocusSource();
        this.clearServerSource();
    }

    public clearDropItem(): void {
        this.dropItem = { ...this.dropItemOriginal };
    }

    public clearBackgroundItem(): void {
        this.backgroundItem = { ...this.backgroundItemOriginal };
    }

    public clearFocusSource(): void {
        this.focusSource = { ...this.focusSourceOriginal };
    }

    public clearServerSource(): void {
        for (let index in this.videoServer) {
            let server = this.videoServer[index];
            for (let jndex in server.sources) {
                this.videoServer[index].sources[jndex].positionShow = null;
                this.videoServer[index].sources[jndex].positionScale = null;
            }
        }

        for (let index in this.eventServer) {
            let server = this.eventServer[index];
            for (let jndex in server.sources) {
                this.eventServer[index].sources[jndex].positionShow = null;
                this.eventServer[index].sources[jndex].positionScale = null;
            }
        }

        for (let index in this.trackServer) {
            let server = this.trackServer[index];
            for (let jndex in server.sources) {
                this.trackServer[index].sources[jndex].positionShow = null;
                this.trackServer[index].sources[jndex].positionScale = null;
            }
        }
    }
    //#endregion clear
}
