















































































import { Vue, Component, Prop, Watch } from 'vue-property-decorator';

// Model
import { EmapItem, EEmapType, EEmapMode, EEmapDeviceType } from './index';
import { IEmapSource } from './models/interfaces';

// Component
import EmapPreviewSource from './vues/preview-source.vue';
import EmapPreviewEmptySource from './vues/preview-empty-source.vue';

import EmapImageBoxEmpty from './vues/image-box-empty.vue';
import EmapImageBoxSource from './vues/image-box-source.vue';

import EmapDeviceListSource from './vues/device-list-source.vue';

@Component({
    components: {
        EmapPreviewSource,
        EmapPreviewEmptySource,

        EmapImageBoxEmpty,
        EmapImageBoxSource,

        EmapDeviceListSource,
    },
})
export default class Emap extends Vue {
    //#region prop
    @Prop({
        type: Object,
        required: false,
        default: () => {
            return new EmapItem();
        },
    })
    value: EmapItem;

    @Prop({
        type: String,
        required: false,
        default: () => {
            return EEmapType.none;
        },
    })
    type: EEmapType;

    @Prop({
        type: String,
        required: false,
        default: () => {
            return EEmapMode.none;
        },
    })
    mode: EEmapMode;

    @Prop({
        type: Boolean,
        required: false,
        default: false,
    })
    modifyDisabled: boolean;
    //#endregion prop

    //#region watch
    @Watch('value.backgroundItem.image', { immediate: true })
    private onBackgroundChange(newval: EmapItem, oldval: EmapItem) {
        this.setScale();
    }
    //#endregion watch

    //#region variable
    private dropTarget: {
        image: boolean;
        map: boolean;
        list: boolean;
    } = {
        image: false,
        map: false,
        list: false,
    };

    private enumItem = {
        eEmapType: EEmapType,
        eEmapMode: EEmapMode,
    };
    //#endregion variable

    //#region Vue Life
    private mounted(): void {
        this.setScale();
        let self = this;
        window.onresize = function (event) {
            self.setScale();
        };
    }
    //#endregion Vue Life

    //#region scale
    private setScale(): void {
        let imageBoxSourceRef: any = this.$refs.imageBoxSource;

        if (imageBoxSourceRef) {
            imageBoxSourceRef.setScale();
        }
    }
    //#endregion scale

    //#region check & change
    private checkTypes(types: EEmapType[]): boolean {
        let result = false;
        for (let type of types) {
            if (this.type == type) {
                result = true;
                break;
            }
        }
        return result;
    }

    private checkMode(mode: EEmapMode): boolean {
        return mode == this.mode ? true : false;
    }

    private checkImage(): boolean {
        return this.value.backgroundItem.image !== '';
    }
    //#endregion check & change

    //#region click
    private clickSource(event: any, source: IEmapSource): void {
        if (this.resolveIsDraggable()) {
            this.value.focusSource = source;
        } else {
            this.value.clearFocusSource();
        }
    }
    //#endregion click

    //#region resolve
    private resolveIsDraggable(): boolean {
        return this.value.backgroundItem.image ? true : false;
    }
    //#endregion resolve

    //#region emit
    private inputModel() {
        this.$emit('input', this.value);
    }

    private addEmap() {
        this.$emit('modify-emap');
    }

    private viewMap() {
        this.$emit('modify-emap');
    }
    //#endregion emit

    //#region reset
    public resetSourceListType() {
        let deviceListSourceRef: any = this.$refs.deviceListSource;
        if (deviceListSourceRef) {
            deviceListSourceRef.clickSourceType(EEmapDeviceType.videoSource);
        }
    }

    public resetBindingListServerType() {
        let deviceListBindingRef: any = this.$refs.deviceListBinding;
        if (deviceListBindingRef) {
            deviceListBindingRef.initFocusServer();
        }
    }
    //#endregion reset
}

Vue.component('e-map', Emap);
