






























































































































































import { Vue, Component, Prop } from 'vue-property-decorator';

import { EmapItem, IEmapBackgroundItem, IEmapSource } from '../index';

import { EmapDialog } from '../helps/dialog';

@Component({
    components: {},
})
export default class EmapImageBoxSource extends Vue {
    //#region prop
    @Prop({
        type: Object,
        required: false,
        default: () => {
            return new EmapItem();
        },
    })
    value: EmapItem;

    @Prop({
        type: Object,
        required: false,
        default: () => {
            return {
                image: false,
                map: false,
                list: false,
            };
        },
    })
    dropTarget: { image: boolean; map: boolean; list: boolean };

    @Prop({
        type: Boolean,
        required: false,
        default: false,
    })
    modifyDisabled: boolean;
    //#endregion prop

    //#region variable
    private imageEventSource = require('../images/device-source-event.svg');
    private imageVideoSource = require('../images/device-source-video.svg');
    private imageTrackSource = require('../images/device-source-track.svg');
    //#endregion variable

    //#region Vue Life
    private mounted(): void {
        this.setScale();
        let self = this;
        window.onresize = function (event) {
            self.setScale();
        };
    }
    //#endregion Vue Life

    //#region Drag & Drop
    private dropMap(event: any): boolean {
        event.preventDefault();

        if (this.modifyDisabled) {
            return false;
        }

        let emapDeviceInfoRef: any = this.$refs.emapDeviceInfo;
        let left = event.layerX + this.value.dropItem.mousePosition.x - this.value.dropItem.size.width / 2;
        let top = event.layerY + this.value.dropItem.mousePosition.y - this.value.dropItem.size.height / 2;
        this.dropTarget.map = false;

        // positionShow
        if (this.value.dropItem.source.positionShow == null) {
            this.value.dropItem.source.positionShow = {
                x: 0,
                y: 0,
            };
        }
        this.value.dropItem.source.positionShow.x = left;
        this.value.dropItem.source.positionShow.y = top;

        // resolve source scale
        if (
            !this.value.backgroundItem.showSize ||
            !this.value.backgroundItem.showSize.width ||
            !this.value.backgroundItem.showSize.height ||
            isNaN(this.value.backgroundItem.showSize.width) ||
            isNaN(this.value.backgroundItem.showSize.height) ||
            this.value.backgroundItem.showSize.width <= 0 ||
            this.value.backgroundItem.showSize.height <= 0
        ) {
            this.setScale();
        }

        if (
            this.value.backgroundItem.showSize &&
            this.value.backgroundItem.showSize.width &&
            this.value.backgroundItem.showSize.height &&
            !isNaN(this.value.backgroundItem.showSize.width) &&
            !isNaN(this.value.backgroundItem.showSize.height) &&
            this.value.backgroundItem.showSize.width > 0 &&
            this.value.backgroundItem.showSize.height > 0
        ) {
            this.value.dropItem.source.positionScale = {
                horizontal: left / this.value.backgroundItem.showSize.width,
                vertical: top / this.value.backgroundItem.showSize.height,
            };
        }

        this.value.clearDropItem();
        if (emapDeviceInfoRef) {
            emapDeviceInfoRef.initSelectionItem();
        }
    }

    private dragMapOver(event: any): boolean {
        event.preventDefault();

        if (this.modifyDisabled) {
            return false;
        }
        this.dropTarget.map = true;
    }

    private dragMapLeave(event: any): boolean {
        if (this.modifyDisabled) {
            return false;
        }
        this.dropTarget.map = false;
    }

    private dragSourceStart(event: any, source: IEmapSource): boolean {
        if (this.modifyDisabled) {
            return false;
        }
        this.value.dropItem.source = source;
        this.value.focusSource = source;
        if (event.offsetX && event.offsetY) {
            this.value.dropItem.mousePosition.x = event.offsetX;
            this.value.dropItem.mousePosition.y = event.offsetY;
        }
        if (event.srcElement && event.srcElement.clientWidth && event.srcElement.clientHeight) {
            this.value.dropItem.size.width = event.srcElement.clientWidth;
            this.value.dropItem.size.height = event.srcElement.clientHeight;
        }
    }

    private dragSourceOver(event: any, source: IEmapSource): void {
        // 判斷上很常觸發, 所以先不做事
        // this.value.clearDropItem();
    }
    //#endregion Drag & Drop

    //#region source
    private showSourceActive(source: IEmapSource): boolean {
        return source.positionShow !== null && !isNaN(source.positionShow.x) && !isNaN(source.positionShow.y);
    }

    private showSourceSelection(source: IEmapSource): boolean {
        return source.objectId === this.value.focusSource.objectId;
    }
    //#endregion source

    //#region resolve
    private resolveIsDraggable(): boolean {
        if (this.modifyDisabled) {
            return false;
        }
        if (this.value.backgroundItem.image != '') {
            return true;
        }
        return false;
    }

    private resolveSourceKey(key: string, source: IEmapSource, serverIndex: number, sourceIndex: number): string {
        return `${key}-${serverIndex}-${sourceIndex}-${source.index}`;
    }
    //#endregion resolve

    //#region image
    private removeImage() {
        EmapDialog.Remove('', this.value.i18n.removeTitle, this.value.i18n.removeButtonYes, this.value.i18n.removeButtonNo)
            // EmapDialog.Remove(this.value.i18n.removeContent, this.value.i18n.removeTitle, this.value.i18n.removeButtonYes, this.value.i18n.removeButtonNo)
            .then((result: any) => {
                if (result.isDismissed) {
                    this.clearImageItem();
                }
            })
            .catch((e: any) => {
                console.error(e);
            });
    }

    private clearImageItem() {
        this.value.clearSelf();
    }
    //#endregion image

    //#region set scale
    private setScale() {
        let modifyImageDivRef: any = this.$refs.modifyImageDiv;

        // Maybe no yet get ref
        if (!modifyImageDivRef) {
            return false;
        }

        if (
            modifyImageDivRef &&
            modifyImageDivRef.clientWidth &&
            modifyImageDivRef.clientHeight &&
            modifyImageDivRef.clientWidth > 0 &&
            modifyImageDivRef.clientHeight > 0
        ) {
            let backgroundItem: IEmapBackgroundItem = JSON.parse(JSON.stringify(this.value.backgroundItem));
            backgroundItem.showSize = {
                width: modifyImageDivRef.clientWidth,
                height: modifyImageDivRef.clientHeight,
            };
            if (
                backgroundItem.showSize.width != this.value.backgroundItem.showSize.width ||
                backgroundItem.showSize.height != this.value.backgroundItem.showSize.height
            ) {
                this.value.backgroundItem = backgroundItem;
            }
        }

        this.value.resetSourcePosition();
    }
    //#endregion set scale

    //#region emit
    private clickSource(event: any, source: IEmapSource) {
        this.$emit('click-source', event, source);
    }

    private inputModel() {
        this.$emit('input', this.value);
    }
    //#endregion emit
}
