


































import { Vue, Component } from 'vue-property-decorator';

import { ServiceResponseFilter, ServiceServerConfig, ServiceBase64, ServiceToast, ServiceDialog } from '@/../helpers';

import { ModelsEmap as Model } from './models';

import Forms from '@/../components/form/forms.vue';
import Card from '@/../components/card/card.vue';
import Loading from '@/../components/loading/loading.vue';

// E-Map
import Emap from '@/components/e-map/e-map.vue';
import { EmapItem } from '@/components/e-map';
import { EEmapType, EEmapMode, EEmapDeviceType, EEmapSourceType } from '@/components/e-map';
import { IEmapServer, IEmapSource, IEmapBackgroundItem } from '@/components/e-map';

@Component({
    components: { Card, Forms, Emap },
})
export default class VuePageClass extends Vue {
    //#region Variables

    private formDataOriginal: Model.IFormData = {
        objectId: '',
        name: '',
    };
    private formData: Model.IFormData = { ...this.formDataOriginal };

    private loading = new Loading();

    // E-Map
    private eEmapType = EEmapType;
    private eEmapMode = EEmapMode;
    private emapModifyDisabled = false;
    private eMapItem: EmapItem = new EmapItem();
    // private eMapParamOrigin: IEmapParam = {
    //     imageBase64: '',
    //     sources: [],
    // };

    //#endregion

    //#region Vue Life
    private async beforeCreate(): Promise<void> {}
    private async created(): Promise<void> {}
    private async beforeMount(): Promise<void> {
        await this.initEmap();
    }
    private async mounted(): Promise<void> {}
    private async beforeDestroy(): Promise<void> {}
    private async destroyed(): Promise<void> {}
    //#endregion

    //#region init Emap
    private async initEmap(): Promise<void> {
        this.eMapItem.i18n = {
            or: this.$i18n.Emap_Or,
            previewButtonAddMap: this.$i18n.Emap_Preview_ButtonAddMap,
            previewButtonViewMap: this.$i18n.Emap_Preview_ButtonViewMap,
            modifyEmptyTitle: this.$i18n.Emap_Modify_EmptyTitle,
            modifyEmptyContent: this.$i18n.Emap_Modify_EmptyContent,
            modifyButtonUploadImage: this.$i18n.Emap_Modify_ButtonUploadImage,
            removeTitle: this.$i18n.Emap_RemoveTitle,
            removeContent: this.$i18n.Emap_RemoveContent,
            removeButtonNo: this.$i18n.Emap_RemoveButtonNo,
            removeButtonYes: this.$i18n.Emap_RemoveButtonYes,
            deviceSourceListTitle: this.$i18n.Emap_DeviceSourceListTitle,
            deviceSourceListSubTitle: this.$i18n.Emap_DeviceSourceListSubTitle,
        };

        // Production
        let response = Model.FakeData;
        let videoIndex = 0;
        let eventIndex = 0;
        let trackIndex = 0;
        let videoServer: IEmapServer[] = [];
        let eventServer: IEmapServer[] = [];
        let trackServer: IEmapServer[] = [];

        // Video Source List
        if (response.videoSources && Array.isArray(response.videoSources)) {
            for (let server of response.videoSources) {
                let tempServer: IEmapServer = {
                    objectId: server.serverId,
                    name: server.serverName,
                    sources: [],
                };
                for (let source of server.sources) {
                    videoIndex++;
                    let tempSource: IEmapSource = {
                        objectId: source.objectId,
                        name: source.name,
                        index: videoIndex.toString().padStart(2, '0'),
                        sourceType: EEmapSourceType[server.serverType],
                        deviceType: EEmapDeviceType.videoSource,
                        positionScale: null,
                        positionShow: null,
                    };
                    tempSource = this.emapSourceInSource(source, tempSource, response);
                    tempServer.sources.push(tempSource);
                }
                videoServer.push(tempServer);
            }
        }

        // Event Source List
        if (response.eventSources && Array.isArray(response.eventSources)) {
            for (let server of response.eventSources) {
                let tempServer: IEmapServer = {
                    objectId: server.serverId,
                    name: server.serverName,
                    sources: [],
                };
                for (let source of server.sources) {
                    eventIndex++;
                    let tempSource: IEmapSource = {
                        objectId: source.objectId,
                        name: source.name,
                        index: eventIndex.toString().padStart(2, '0'),
                        sourceType: EEmapSourceType[server.serverType],
                        deviceType: EEmapDeviceType.eventSource,
                        positionScale: null,
                        positionShow: null,
                    };
                    tempSource = this.emapSourceInSource(source, tempSource, response);
                    tempServer.sources.push(tempSource);
                }
                eventServer.push(tempServer);
            }
        }

        // Track Source List
        if (response.trackSources && Array.isArray(response.trackSources)) {
            for (let server of response.trackSources) {
                let tempServer: IEmapServer = {
                    objectId: server.serverId,
                    name: server.serverName,
                    sources: [],
                };
                for (let source of server.sources) {
                    trackIndex++;
                    let tempSource: IEmapSource = {
                        objectId: source.objectId,
                        name: source.name,
                        index: trackIndex.toString().padStart(2, '0'),
                        sourceType: EEmapSourceType[server.serverType],
                        deviceType: EEmapDeviceType.trackSource,
                        positionScale: null,
                        positionShow: null,
                    };
                    tempSource = this.emapSourceInSource(source, tempSource, response);
                    tempServer.sources.push(tempSource);
                }
                trackServer.push(tempServer);
            }
        }

        this.eMapItem.videoServer = videoServer;
        this.eMapItem.eventServer = eventServer;
        this.eMapItem.trackServer = trackServer;

        if (response.map) {
            this.eMapItem.objectId = response.map.objectId ? response.map.objectId : '';
            if (response.map.imageSrc) {
                // let imageUrl = ServiceServerConfig.url('/' + response.map.imageSrc);
                let imageUrl = response.map.imageSrc;
                let base64: string = await ServiceBase64.urlToBase64(imageUrl);
                if (base64) {
                    this.eMapItem.backgroundItem.image = base64;
                    this.resetEmapScale();
                }
            }
        }
    }

    private emapSourceInSource(source, tempSource, response): any {
        if (response.map && response.map.sources && Array.isArray(response.map.sources)) {
            for (let mapSource of response.map.sources) {
                if (source.objectId == mapSource.eventSourceId) {
                    tempSource.cameraAngle = mapSource.angle && !isNaN(parseInt(mapSource.angle)) ? parseInt(mapSource.angle) : 0;

                    if (mapSource.x && mapSource.y && !isNaN(parseFloat(mapSource.x)) && !isNaN(parseFloat(mapSource.y))) {
                        tempSource.positionScale = {
                            horizontal: parseFloat(mapSource.x),
                            vertical: parseFloat(mapSource.y),
                        };
                    }
                    break;
                }
            }
        }
        return tempSource;
    }

    private eMapReset() {
        this.eMapItem.resetSelf();
    }

    private resetEmapScale() {
        let emapModifyRef: any = this.$refs.emapModify;
        setTimeout(() => {
            if (emapModifyRef) {
                emapModifyRef.setScale();
            }
        }, 500);
    }

    private resolveSourceType(value: string): EEmapSourceType {
        let result = EEmapSourceType.none;

        switch (value) {
            case 'frsm':
                result = EEmapSourceType.frsManager;
                break;
            case 'cms':
                result = EEmapSourceType.cms;
                break;
            case 'sis':
                result = EEmapSourceType.sis;
                break;
        }
        return EEmapSourceType[value];
    }
}
//#endregion
