



















































import { Vue, Component, Prop, Emit, Watch } from 'vue-property-decorator';

import { EmapItem } from '../index';

// Service
import { ServiceBase64 } from 'web-service-base64';

@Component({
    components: {},
})
export class EmapImageBoxEmpty extends Vue {
    //////////////////// Prop ////////////////////
    @Prop({
        type: Object,
        required: false,
        default: () => {
            return new EmapItem();
        },
    })
    value: EmapItem;

    @Prop({
        type: Object,
        required: false,
        default: () => {
            return {
                image: false,
                map: false,
                list: false,
            };
        },
    })
    dropTarget: { image: boolean; map: boolean; list: boolean };

    @Prop({
        type: Boolean,
        required: false,
        default: false,
    })
    modifyDisabled: boolean;

    private imageEmpty = require('../images/modify-empty.svg');

    //////////////////// Vue Life ////////////////////

    private async beforeCreate() {}
    private async created() {}
    private async beforeMount() {}
    private async mounted() {}
    private async beforeDestroy() {}
    private async destroyed() {}

    //////////////////// Drop & Drag ////////////////////

    private dropImage(event: any) {
        event.preventDefault();
        if (this.modifyDisabled) {
            return false;
        }
        this.dropTarget.image = false;
        if (event.dataTransfer && event.dataTransfer.files && event.dataTransfer.files[0]) {
            this.uploadImage(event.dataTransfer.files[0]);
        }
    }

    private dragImageOver(event: any) {
        event.preventDefault();
        if (this.modifyDisabled) {
            return false;
        }
        this.dropTarget.image = true;
    }
    private dragImageLeave(event: any) {
        if (this.modifyDisabled) {
            return false;
        }
        this.dropTarget.image = false;
    }

    //////////////////// Page Control ////////////////////

    private getInputFile() {
        if (this.modifyDisabled) {
            return false;
        }
        let element: any = this.$refs.uploadImage;
        if (element) {
            this.uploadImage(element.files[0]);
            element.value = '';
        }
    }

    private async uploadImage(file: File | null) {
        if (this.modifyDisabled) {
            return false;
        }
        if (file) {
            let base64: any = await ServiceBase64.getBase64(file);
            if (base64.result) {
                this.value.backgroundItem.image = base64.result;
                this.$emit('set-scale');
            }
        }
    }

    private triggerUploadImage() {
        if (this.modifyDisabled) {
            return false;
        }
        let element: any = this.$refs.uploadImage;
        if (element) {
            element.click();
        }
    }

    private inputModel() {
        this.$emit('input', this.value);
    }
}
export default EmapImageBoxEmpty;
Vue.component('emap-image-box-empty', EmapImageBoxEmpty);
