import Swal, { SweetAlertResult } from 'sweetalert2/dist/sweetalert2.js';

export namespace EmapDialog {
    /**
     * @param  {any} vue
     * @param  {string} content
     * @param  {string} title?
     * @param  {string} confirmButtonText?
     * @param  {string} cancelButtonText?
     * @param  {string} iconTitle?
     * @param  {number} timer?
     * @returns Promise
     */
    export function Remove(
        content: string,
        title: string,
        confirmButtonText: string,
        cancelButtonText: string,
        timer?: number,
    ): Promise<SweetAlertResult> {
        return Swal.fire({
            title: title,
            text: content,
            allowOutsideClick: false,
            showConfirmButton: true,
            confirmButtonColor: '#537895',
            confirmButtonText: `${cancelButtonText}`,
            showCancelButton: true,
            cancelButtonColor: '#FFFFFF',
            cancelButtonText: `${confirmButtonText}`,
            timer: timer === undefined ? undefined : timer,
            customClass: {
                popup: 'emap-dialog-popup',
                header: 'emap-dialog-header',
                title: 'emap-dialog-title',
                content: 'emap-dialog-content',
                closeButton: 'close-button-class',
                confirmButton: 'confirm-button-class',
                cancelButton: 'cancel-button-class',
            },
        });
    }
}
