




































































import { Vue, Component, Prop, Emit, Watch } from 'vue-property-decorator';

// Model
import { EmapItem, EEmapServerType, EEmapDeviceType } from '../index';

@Component({
    components: {},
})
export class EmapPreviewSource extends Vue {
    //////////////////// Prop ////////////////////
    @Prop({
        type: Object,
        required: false,
        default: () => {
            return new EmapItem();
        },
    })
    value: EmapItem;

    @Prop({
        type: Boolean,
        required: false,
        default: false,
    })
    modifyDisabled: boolean;

    eEmapDeviceType = EEmapDeviceType;

    //////////////////// Vue Life ////////////////////

    private async beforeCreate() {}
    private async created() {}
    private async beforeMount() {}
    private async mounted() {}
    private async beforeDestroy() {}
    private async destroyed() {}

    //////////////////// Page Control ////////////////////

    private resolveSourceLength(deviceType: EEmapDeviceType): string {
        let result = 0;
        let serverType = EEmapServerType.none;
        switch (deviceType) {
            case EEmapDeviceType.videoSource:
                serverType = EEmapServerType.videoServer;
                break;
            case EEmapDeviceType.eventSource:
                serverType = EEmapServerType.eventServer;
                break;
        }

        if (this.value[serverType]) {
            for (let server of this.value[serverType]) {
                if (!isNaN(server.sources.length)) {
                    result += server.sources.length;
                }
            }
        }

        return result.toString();
    }

    private viewMap() {
        this.$emit('view-emap');
    }

    private inputModel() {
        this.$emit('input', this.value);
    }
}
export default EmapPreviewSource;
Vue.component('emap-preview-source', EmapPreviewSource);
