










































































































































import { Vue, Component, Prop, Watch } from 'vue-property-decorator';

import { EmapItem, EEmapDeviceType, IEmapSource } from '../index';

@Component({
    components: {},
})
export default class EmapDeviceListSource extends Vue {
    //#region prop
    @Prop({
        type: Object,
        required: false,
        default: () => new EmapItem(),
    })
    value: EmapItem;

    @Prop({
        type: Object,
        required: false,
        default: () => {
            return {
                image: false,
                map: false,
                list: false,
            };
        },
    })
    dropTarget: { image: boolean; map: boolean; list: boolean };

    @Prop({
        type: Boolean,
        required: false,
        default: false,
    })
    modifyDisabled: boolean;
    //#endregion prop

    //#region variable
    private sourceListType: EEmapDeviceType = EEmapDeviceType.videoSource;
    private eEmapDeviceType = EEmapDeviceType;

    private toggleItem: {
        isVideoListDropdownActive: boolean;
        isEventListDropdownActive: boolean;
        isTrackListDropdownActive: boolean;
    } = {
        isVideoListDropdownActive: false,
        isEventListDropdownActive: false,
        isTrackListDropdownActive: false,
    };
    //#endregion variable

    //#region
    @Watch('value', { immediate: true, deep: true })
    private valueChanged(newVal: EmapItem, oldVal: EmapItem): void {
        switch (newVal?.focusSource?.deviceType) {
            case EEmapDeviceType.videoSource:
                this.toggleItem.isVideoListDropdownActive = true;
                break;
            case EEmapDeviceType.eventSource:
                this.toggleItem.isEventListDropdownActive = true;
                break;
            case EEmapDeviceType.trackSource:
                this.toggleItem.isTrackListDropdownActive = true;
                break;
            default:
                break;
        }
    }
    //#endregion watch

    //#endregion Drag & Drop
    private dropList(event: any): boolean {
        event.preventDefault();

        if (this.modifyDisabled) {
            return false;
        }

        this.dropTarget.list = false;
        if (!this.value.focusSource || !this.value.focusSource.objectId) {
            return false;
        }
        if (this.value.focusSource.deviceType == EEmapDeviceType.eventSource) {
        }
        this.value.focusSource.positionShow = null;
        this.value.focusSource.positionScale = null;
    }

    private dragListOver(event: any): boolean {
        event.preventDefault();

        if (this.modifyDisabled) {
            return false;
        }
        this.dropTarget.list = true;
    }

    private dragListLeave(event: any): boolean {
        if (this.modifyDisabled) {
            return false;
        }
        this.dropTarget.list = false;
    }

    private dragSourceStart(event: any, source: IEmapSource): boolean {
        if (this.modifyDisabled) {
            return false;
        }
        this.value.dropItem.source = source;
        this.value.focusSource = source;
        if (event.offsetX && event.offsetY) {
            this.value.dropItem.mousePosition.x = event.offsetX;
            this.value.dropItem.mousePosition.y = event.offsetY;
        }
        if (event.srcElement && event.srcElement.clientWidth && event.srcElement.clientHeight) {
            this.value.dropItem.size.width = event.srcElement.clientWidth;
            this.value.dropItem.size.height = event.srcElement.clientHeight;
        }
    }

    private dragSourceOver(event: any, source: IEmapSource): void {
        // 判斷上很常觸發, 所以先不做事
        // this.value.clearDropItem();
    }
    //#endregion Drag & Drop

    //#region source
    private toggleSourceList(type: EEmapDeviceType, index: number): void {
        let tempServer = JSON.parse(JSON.stringify(this.value.videoServer));
        switch (type) {
            case EEmapDeviceType.videoSource:
                this.toggleItem.isVideoListDropdownActive = !this.toggleItem.isVideoListDropdownActive;
                tempServer = JSON.parse(JSON.stringify(this.value.videoServer));
                this.value.videoServer = tempServer;
                break;
            case EEmapDeviceType.eventSource:
                this.toggleItem.isEventListDropdownActive = !this.toggleItem.isEventListDropdownActive;
                tempServer = JSON.parse(JSON.stringify(this.value.eventServer));
                this.value.eventServer = tempServer;
                break;
            case EEmapDeviceType.trackSource:
                this.toggleItem.isTrackListDropdownActive = !this.toggleItem.isTrackListDropdownActive;
                tempServer = JSON.parse(JSON.stringify(this.value.trackServer));
                this.value.trackServer = tempServer;
                break;
            default:
                break;
        }
    }

    private showSourceActive(source: IEmapSource): boolean {
        return source.positionShow !== null && !isNaN(source.positionShow.x) && !isNaN(source.positionShow.y);
    }

    private showSourceSelection(source: IEmapSource): boolean {
        return source.objectId === this.value.focusSource.objectId;
    }
    //#endregion source

    //#region resolve
    private resolveIsDraggable(): boolean {
        return this.value.backgroundItem.image ? true : false;
    }

    private resolveSourceKey(key: string, source: IEmapSource, serverIndex: number, sourceIndex: number): string {
        return `${key}-${serverIndex}-${sourceIndex}-${source.index}`;
    }
    //#endregion resolve

    //#region emit
    private clickSource(event: any, source: IEmapSource): void {
        this.$emit('click-source', event, source);
    }

    private inputModel(): void {
        this.$emit('input', this.value);
    }
    //#endregion emit
}
