































import { Vue, Component, Prop, Emit, Watch } from 'vue-property-decorator';

// Model
import { EmapItem } from '../index';

@Component({
    components: {},
})
export class EmapPreviewEmptySource extends Vue {
    //////////////////// Prop ////////////////////
    @Prop({
        type: Object,
        required: false,
        default: () => {
            return new EmapItem();
        },
    })
    value: EmapItem;

    @Prop({
        type: Boolean,
        required: false,
        default: false,
    })
    modifyDisabled: boolean;

    private imageEmpty = require('../images/preview-empty-source.svg');

    //////////////////// Vue Life ////////////////////

    private async beforeCreate() {}
    private async created() {}
    private async beforeMount() {}
    private async mounted() {}
    private async beforeDestroy() {}
    private async destroyed() {}

    //////////////////// Page Control ////////////////////

    private addEmap() {
        this.$emit('add-emap');
    }
}
export default EmapPreviewEmptySource;
Vue.component('emap-preview-empty-source', EmapPreviewEmptySource);
